import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import {
  Container,
  Grid,
  Paper,
  Typography,
  Box,
  Card,
  CardContent,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Chip,
  CircularProgress,
  IconButton,
  Tooltip,
  LinearProgress,
  Divider,
  Alert
} from '@mui/material';
import {
  Work,
  People,
  Assessment,
  TrendingUp,
  Add,
  Visibility,
  Edit,
  Schedule,
  LocationOn,
  AttachMoney,
  CheckCircle,
  Cancel,
  HourglassEmpty,
  Notifications,
  Search,
  Message,
  CalendarToday,
  Star,
  FilterList,
  Person
} from '@mui/icons-material';
import { 
  collection, 
  query, 
  where, 
  getDocs, 
  orderBy, 
  limit,
  Timestamp,
  getDoc,
  doc
} from 'firebase/firestore';
import { db } from '../../config/firebase';
import { useAuth } from '../../contexts/AuthContext';
import { JobPosting, JobApplication, EmployerProfile } from '../../types';

interface EmployerProfileWithId extends EmployerProfile {
  id: string;
}

interface DashboardStats {
  activeJobs: number;
  totalApplications: number;
  pendingApplications: number;
  acceptedApplications: number;
  viewsThisWeek: number;
  responseRate: number;
  averageTimeToHire: number;
  topPerformingJob: string;
}

interface RecentActivity {
  id: string;
  type: 'application' | 'message' | 'job_view' | 'interview';
  title: string;
  description: string;
  timestamp: Date;
  relatedId?: string;
}

const EmployerDashboard: React.FC = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const { currentUser, userData } = useAuth();
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState<DashboardStats>({
    activeJobs: 0,
    totalApplications: 0,
    pendingApplications: 0,
    acceptedApplications: 0,
    viewsThisWeek: 0,
    responseRate: 0,
    averageTimeToHire: 0,
    topPerformingJob: ''
  });
  const [recentJobs, setRecentJobs] = useState<JobPosting[]>([]);
  const [recentApplications, setRecentApplications] = useState<JobApplication[]>([]);
  const [recentActivity, setRecentActivity] = useState<RecentActivity[]>([]);
  const [employerProfile, setEmployerProfile] = useState<EmployerProfileWithId | null>(null);

  useEffect(() => {
    if (currentUser && userData?.role === 'employer') {
      fetchDashboardData();
    }
  }, [currentUser, userData]);

  const fetchDashboardData = async () => {
    if (!currentUser) return;
    
    setLoading(true);
    try {
      // Fetch employer profile
      const profileQuery = query(
        collection(db, 'employerProfiles'),
        where('userId', '==', currentUser.uid)
      );
      const profileSnapshot = await getDocs(profileQuery);
      if (!profileSnapshot.empty) {
        const profileData = profileSnapshot.docs[0].data() as EmployerProfile;
        setEmployerProfile({
          id: profileSnapshot.docs[0].id,
          ...profileData
        });
      }

      // Fetch jobs
      const jobsQuery = query(
        collection(db, 'jobs'),
        where('employerId', '==', currentUser.uid),
        orderBy('createdAt', 'desc')
      );
      const jobsSnapshot = await getDocs(jobsQuery);
      const jobsList = jobsSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      })) as JobPosting[];
      
      setRecentJobs(jobsList.slice(0, 5));
      
      // Calculate active jobs
      const activeJobsCount = jobsList.filter(job => job.status === 'open').length;
      
      // Fetch all applications for employer's jobs
      const jobIds = jobsList.map(job => job.id);
      let allApplications: JobApplication[] = [];
      
      if (jobIds.length > 0) {
        // Firestore has a limit for 'in' queries, so we need to batch
        const batchSize = 10;
        for (let i = 0; i < jobIds.length; i += batchSize) {
          const batch = jobIds.slice(i, i + batchSize);
          const appsQuery = query(
            collection(db, 'applications'),
            where('jobId', 'in', batch)
          );
          const appsSnapshot = await getDocs(appsQuery);
          const apps = appsSnapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
          })) as JobApplication[];
          allApplications = [...allApplications, ...apps];
        }
      }
      
      // Sort applications by date
      allApplications.sort((a, b) => {
        const dateA = a.appliedAt?.toDate?.() || new Date(0);
        const dateB = b.appliedAt?.toDate?.() || new Date(0);
        return dateB.getTime() - dateA.getTime();
      });
      
      setRecentApplications(allApplications.slice(0, 5));
      
      // Calculate stats
      const pendingApps = allApplications.filter(app => app.status === 'pending').length;
      const acceptedApps = allApplications.filter(app => app.status === 'accepted').length;
      
      // Calculate response rate
      const respondedApps = allApplications.filter(app => app.status !== 'pending').length;
      const responseRate = allApplications.length > 0 
        ? Math.round((respondedApps / allApplications.length) * 100)
        : 0;
      
      // Find top performing job (most applications)
      const jobApplicationCounts = jobsList.map(job => ({
        title: job.title,
        count: allApplications.filter(app => app.jobId === job.id).length
      }));
      const topJob = jobApplicationCounts.reduce((prev, current) => 
        (prev.count > current.count) ? prev : current, 
        { title: '', count: 0 }
      );
      
      // Generate recent activity
      const activities: RecentActivity[] = [];
      
      // Add recent applications as activities
      allApplications.slice(0, 3).forEach(app => {
        const job = jobsList.find(j => j.id === app.jobId);
        activities.push({
          id: app.id,
          type: 'application',
          title: 'Nueva aplicación recibida',
          description: `${app.workerName} aplicó para ${job?.title || 'un trabajo'}`,
          timestamp: app.appliedAt?.toDate?.() || new Date(),
          relatedId: app.jobId
        });
      });
      
      // Sort activities by timestamp
      activities.sort((a, b) => b.timestamp.getTime() - a.timestamp.getTime());
      setRecentActivity(activities);
      
      setStats({
        activeJobs: activeJobsCount,
        totalApplications: allApplications.length,
        pendingApplications: pendingApps,
        acceptedApplications: acceptedApps,
        viewsThisWeek: Math.floor(Math.random() * 500) + 100, // Placeholder
        responseRate,
        averageTimeToHire: 5, // Placeholder
        topPerformingJob: topJob.title || 'N/A'
      });
      
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    } finally {
      setLoading(false);
    }
  };

  const getApplicationStatusColor = (status: string) => {
    switch (status) {
      case 'pending': return 'warning';
      case 'accepted': return 'success';
      case 'rejected': return 'error';
      default: return 'default';
    }
  };

  const getActivityIcon = (type: string) => {
    switch (type) {
      case 'application': return <People />;
      case 'message': return <Message />;
      case 'job_view': return <Visibility />;
      case 'interview': return <CalendarToday />;
      default: return <Notifications />;
    }
  };

  if (loading) {
    return (
      <Container sx={{ py: 4, textAlign: 'center' }}>
        <CircularProgress />
      </Container>
    );
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/* Header */}
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" gutterBottom>
          Panel de Empleador
        </Typography>
        <Typography variant="body1" color="text.secondary">
          Bienvenido de vuelta, {employerProfile?.companyName || userData?.email}
        </Typography>
      </Box>

      {/* Quick Actions */}
      <Grid container spacing={2} sx={{ mb: 4 }}>
        <Grid item>
          <Button
            variant="contained"
            startIcon={<Add />}
            onClick={() => navigate('/employer/post-job')}
            size="large"
          >
            Publicar Trabajo
          </Button>
        </Grid>
        <Grid item>
          <Button
            variant="outlined"
            startIcon={<People />}
            onClick={() => navigate('/employer/applications')}
          >
            Ver Aplicaciones
          </Button>
        </Grid>
        <Grid item>
          <Button
            variant="outlined"
            startIcon={<Search />}
            onClick={() => navigate('/workers')}
          >
            Buscar Trabajadores
          </Button>
        </Grid>
        <Grid item>
          <Button
            variant="outlined"
            startIcon={<Message />}
            onClick={() => navigate('/employer/messages')}
          >
            Mensajes
          </Button>
        </Grid>
      </Grid>

      {/* Stats Grid */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={6} md={3}>
          <Card elevation={2}>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center">
                <Box>
                  <Typography color="text.secondary" variant="body2" gutterBottom>
                    Trabajos Activos
                  </Typography>
                  <Typography variant="h4">
                    {stats.activeJobs}
                  </Typography>
                </Box>
                <Avatar sx={{ bgcolor: 'primary.main' }}>
                  <Work />
                </Avatar>
              </Box>
              <Box sx={{ mt: 2 }}>
                <LinearProgress 
                  variant="determinate" 
                  value={75} 
                  sx={{ height: 6, borderRadius: 3 }}
                />
                <Typography variant="caption" color="text.secondary" sx={{ mt: 1 }}>
                  3 expiran esta semana
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card elevation={2}>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center">
                <Box>
                  <Typography color="text.secondary" variant="body2" gutterBottom>
                    Total Aplicaciones
                  </Typography>
                  <Typography variant="h4">
                    {stats.totalApplications}
                  </Typography>
                </Box>
                <Avatar sx={{ bgcolor: 'success.main' }}>
                  <People />
                </Avatar>
              </Box>
              <Box sx={{ mt: 2, display: 'flex', gap: 1 }}>
                <Chip 
                  size="small" 
                  label={`${stats.pendingApplications} pendientes`}
                  color="warning"
                />
                <Chip 
                  size="small" 
                  label={`${stats.acceptedApplications} aceptadas`}
                  color="success"
                />
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card elevation={2}>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center">
                <Box>
                  <Typography color="text.secondary" variant="body2" gutterBottom>
                    Tasa de Respuesta
                  </Typography>
                  <Typography variant="h4">
                    {stats.responseRate}%
                  </Typography>
                </Box>
                <Avatar sx={{ bgcolor: 'warning.main' }}>
                  <TrendingUp />
                </Avatar>
              </Box>
              <Box sx={{ mt: 2 }}>
                <Typography variant="caption" color="text.secondary">
                  Promedio: {stats.averageTimeToHire} días para contratar
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card elevation={2}>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center">
                <Box>
                  <Typography color="text.secondary" variant="body2" gutterBottom>
                    Vistas Esta Semana
                  </Typography>
                  <Typography variant="h4">
                    {stats.viewsThisWeek}
                  </Typography>
                </Box>
                <Avatar sx={{ bgcolor: 'info.main' }}>
                  <Visibility />
                </Avatar>
              </Box>
              <Box sx={{ mt: 2 }}>
                <Typography variant="caption" color="text.secondary">
                  Top: {stats.topPerformingJob}
                </Typography>
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Grid container spacing={3}>
        {/* Recent Jobs */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, height: '100%' }}>
            <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
              <Typography variant="h6">
                Trabajos Recientes
              </Typography>
              <Button 
                size="small" 
                onClick={() => navigate('/employer/jobs')}
                endIcon={<Visibility />}
              >
                Ver todos
              </Button>
            </Box>
            
            <List>
              {recentJobs.length === 0 ? (
                <ListItem>
                  <ListItemText 
                    primary="No hay trabajos publicados"
                    secondary="Publica tu primer trabajo para comenzar"
                  />
                </ListItem>
              ) : (
                recentJobs.map((job) => (
                  <ListItem 
                    key={job.id}
                    secondaryAction={
                      <Box display="flex" gap={1}>
                        <Tooltip title="Ver aplicaciones">
                          <IconButton 
                            size="small"
                            onClick={() => navigate(`/employer/jobs/${job.id}/applications`)}
                          >
                            <People />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="Editar">
                          <IconButton 
                            size="small"
                            onClick={() => navigate(`/employer/jobs/${job.id}/edit`)}
                          >
                            <Edit />
                          </IconButton>
                        </Tooltip>
                      </Box>
                    }
                  >
                    <ListItemAvatar>
                      <Avatar sx={{ bgcolor: job.status === 'open' ? 'success.light' : 'grey.400' }}>
                        <Work />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText
                      primary={
                        <Box display="flex" alignItems="center" gap={1}>
                          <Typography variant="subtitle1">{job.title}</Typography>
                          <Chip 
                            size="small" 
                            label={job.status === 'open' ? 'Activo' : 'Cerrado'}
                            color={job.status === 'open' ? 'success' : 'default'}
                          />
                        </Box>
                      }
                      secondary={
                        <Box display="flex" alignItems="center" gap={2} mt={0.5}>
                          <Box display="flex" alignItems="center" gap={0.5}>
                            <LocationOn fontSize="small" />
                            <Typography variant="caption">{job.location}</Typography>
                          </Box>
                          <Box display="flex" alignItems="center" gap={0.5}>
                            <AttachMoney fontSize="small" />
                            <Typography variant="caption">
                              ${job.salary.amount}/{job.salary.period}
                            </Typography>
                          </Box>
                          <Box display="flex" alignItems="center" gap={0.5}>
                            <People fontSize="small" />
                            <Typography variant="caption">
                              {job.applicants?.length || 0} aplicantes
                            </Typography>
                          </Box>
                        </Box>
                      }
                    />
                  </ListItem>
                ))
              )}
            </List>
          </Paper>
        </Grid>

        {/* Recent Applications */}
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, height: '100%' }}>
            <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
              <Typography variant="h6">
                Aplicaciones Recientes
              </Typography>
              <Button 
                size="small" 
                onClick={() => navigate('/employer/applications')}
                endIcon={<FilterList />}
              >
                Ver todas
              </Button>
            </Box>
            
            <List>
              {recentApplications.length === 0 ? (
                <ListItem>
                  <ListItemText 
                    primary="No hay aplicaciones recientes"
                    secondary="Las nuevas aplicaciones aparecerán aquí"
                  />
                </ListItem>
              ) : (
                recentApplications.map((application) => (
                  <ListItem 
                    key={application.id}
                    secondaryAction={
                      <Chip 
                        size="small"
                        label={t(`application.status.${application.status}`)}
                        color={getApplicationStatusColor(application.status)}
                      />
                    }
                  >
                    <ListItemAvatar>
                      <Avatar src={application.workerPhoto}>
                        <Person />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText
                      primary={application.workerName}
                      secondary={
                        <Box>
                          <Typography variant="caption" display="block">
                            Aplicó hace {Math.floor(Math.random() * 24) + 1} horas
                          </Typography>
                          {application.workerRating && (
                            <Box display="flex" alignItems="center" gap={0.5}>
                              <Star fontSize="small" sx={{ color: 'warning.main' }} />
                              <Typography variant="caption">
                                {application.workerRating}/5
                              </Typography>
                            </Box>
                          )}
                        </Box>
                      }
                    />
                  </ListItem>
                ))
              )}
            </List>
          </Paper>
        </Grid>

        {/* Recent Activity */}
        <Grid item xs={12}>
          <Paper sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              Actividad Reciente
            </Typography>
            
            {recentActivity.length === 0 ? (
              <Alert severity="info">
                No hay actividad reciente para mostrar
              </Alert>
            ) : (
              <List>
                {recentActivity.map((activity, index) => (
                  <React.Fragment key={activity.id}>
                    <ListItem>
                      <ListItemAvatar>
                        <Avatar sx={{ bgcolor: 'primary.light' }}>
                          {getActivityIcon(activity.type)}
                        </Avatar>
                      </ListItemAvatar>
                      <ListItemText
                        primary={activity.title}
                        secondary={
                          <Box>
                            <Typography variant="body2" color="text.secondary">
                              {activity.description}
                            </Typography>
                            <Typography variant="caption" color="text.secondary">
                              {new Date(activity.timestamp).toLocaleString('es-MX')}
                            </Typography>
                          </Box>
                        }
                      />
                      {activity.relatedId && (
                        <Button 
                          size="small"
                          onClick={() => navigate(`/jobs/${activity.relatedId}`)}
                        >
                          Ver
                        </Button>
                      )}
                    </ListItem>
                    {index < recentActivity.length - 1 && <Divider variant="inset" component="li" />}
                  </React.Fragment>
                ))}
              </List>
            )}
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default EmployerDashboard;